.header {
    &__wrapper {
        background: url('./images/header.jpg');
        background-size: 100% auto;
        background-position: center;
        width: 100%;
        height: 18%;
        padding: 20px;
        display: flex;
        flex-direction: column;
        align-items:flex-end;
        justify-content: space-around;
    }
    &__links {
        display: flex;
        justify-content: space-between;
        width: 420px;
    }
}